/* global $ */

$('.js-nav').each((ind, el) => {
	const $navItems = $(el).children('.nav__item');

	const hideTimers = {};

	const showMenu = id => {
		const $elem = $navItems.filter((index, element) => {
			if ($(element).data('id') === id){
				return $(element);
			}
		});
		$elem.children('.js-sub-nav').css('visibility', 'visible');
		clearTimeout(hideTimers[id]);
	};

	const hideMenu = id => {
		hideTimers[id] = setTimeout(() => {
			const $elem = $navItems.filter((index, element) => {
				if ($(element).data('id') === id){
					return $(element);
				}
			});
			$elem.children('.js-sub-nav').css('visibility', '');
		}, 1000);
	};

	$navItems.on('mouseover', e => {
		showMenu($(e.currentTarget).data('id'));
	});
	$navItems.on('mouseout', e => {
		hideMenu($(e.currentTarget).data('id'));
	});
	$navItems.on('click', e => {
		$navItems.removeClass('is-open');
		$(e.currentTarget).addClass('is-open');
	});

	$(document).on('click', e => {
		if ($(e.target).closest('.nav__item').length === 0){
			$('.nav__item').removeClass('is-open');
		}
	});

	$('.js-sub-nav-prev').on('click', e => {
		e.stopPropagation();
		$('.nav__item').removeClass('is-open');
	});

});



