/* global $ */

// Подстановка первого is-active в head
$('.js-select-tabs').each((ind, el) => {
	const $text = $(el).find('.js-select-tabs-text');
	const $head = $(el).find('.js-select-tabs-head');

	if (!$text.filter('.is-active').length){
		$text.eq(0).addClass('is-active');
	}
	// Происходит перенос из отдельного элемента в другое место, при этом мы уже в начальном месте имеем меньшее количество элементо
	// потому что один из них был перемещен
	$head.html($text.filter('.is-active').clone());
});

// Событие сворачивания
$('.js-select-tabs').on('selectTabs_close', e => {
	$(e.currentTarget).find('.js-select-tabs-head').removeClass('is-active');
	$(e.currentTarget).find('.js-select-tabs-dropdown').slideUp(300);
});

// Клик по шапке выпадающего списка
$('.js-select-tabs-head').on('click', e => {
	$(e.currentTarget).toggleClass('is-active');
	$(e.currentTarget).parents('.js-select-tabs').find('.js-select-tabs-dropdown').eq(0).slideToggle(300);
});

$('.js-select-tabs-text').on('click', e => {
	$(e.currentTarget).parents('.js-select-tabs').trigger('selectTabs_close');
});

// Клик вне блока
$(document).on('click', e => {
	const selectTabs = $('.js-select-tabs');
	if (!$(e.target).closest(selectTabs).length) {
		selectTabs.trigger('selectTabs_close');
	}
});

$('.js-tabs-content').hide();

if ($('.js-select-tabs-text[data-tabs]').length){
	console.log('da');
	const $texts = $('.js-select-tabs-text');
	$(`.js-tabs-content[data-tabs="${$texts.filter('.is-active').data('tabs')}"`).fadeIn(300);
}

// Показ конкретного блока

$('body').on('click', '.js-select-tabs-text', e => {
	if (!$(e.currentTarget).is('.is-active')){
		const id = $(e.currentTarget).data('tabs');

		const $selectTabs     = $(e.currentTarget).parents('.js-select-tabs');
		const $head           = $selectTabs.find('.js-select-tabs-head');
		const $dropDown       = $selectTabs.children('.js-select-tabs-dropdown');
		const $texts          = $dropDown.find('.js-select-tabs-text');

		$head.html($texts.filter(`[data-tabs="${id}"]`).clone().addClass('is-active'));

		$('.js-tabs-content').hide();
		$(`.js-tabs-content[data-tabs="${id}"`).fadeIn(300);

		$texts.removeClass('is-active');
		$(e.currentTarget).addClass('is-active');

	}
	return false;
});




