/* global $ */

import 'owl.carousel';

$('.js-popular-goods').owlCarousel({
	items: 4,
	nav: true,
	dots: false,
	navText: [
		'',
		''
	],
	responsive: {
		0: {
			items: 1
		},
		768: {
			items: 3
		},
		1024: {
			items: 4
		}
	}
});



