/* global $ */

const $icons            = $('.js-icon-help');
const $hiddenBlocks     = $icons.children('.js-hidden-block');
const $buttonMobileMenu = $('.js-open-menu');

// Отвечает за открытие и закрытие меню
// Кнопка меню .js-open-menu
$buttonMobileMenu.on('click', e => {
	$hiddenBlocks.removeClass('is-open');
	if (!$(e.currentTarget).is('.is-open')){
		$('body').css({overflow: 'hidden'});
		$(e.currentTarget).addClass('is-open');
		$('.header-nav').addClass('is-open');
		$('.page-content').addClass('shadow');
		// $('.header__container-nav').addClass('is-active');
	}else {
		$('body').css({overflow: '', position: ''});
		$(e.currentTarget).removeClass('is-open');
		$('.header-nav').removeClass('is-open');
		$('.page-content').removeClass('shadow');
		// $('.header__container-nav').removeClass('is-active');
	}
});


export const checkQuantityGoods = () => {
	const $colsBasket = $('.js-basket-quantity .col');
	const $colText    = $('.js-basket-quantity .col_txt');
	const $colLink    = $('.js-basket-quantity .col_link');

	$colsBasket.removeClass('d-xs-none');
	if (parseInt($('.js-basket-quantity').text(), 10) === 0){
		$colLink.addClass('d-xs-none');
	}else {
		$colText.addClass('d-xs-none');
	}
};

checkQuantityGoods();

$icons.on('click', e => {
	if ($(e.target).hasClass('js-icon-help') && $(e.target).children('.js-hidden-block').hasClass('is-open')){
		$(e.target).children().toggleClass('is-open');
	}else if ($(e.target).hasClass('js-icon-help')){
		$hiddenBlocks.removeClass('is-open');

		// Закрываем меню удаляя все классы
		$('.js-open-menu.is-open').trigger('click');

		$(e.target).children().toggleClass('is-open');
	}
});

$(document).on('click', e => {
	if ($(e.target).closest('.js-icon-help').length === 0) {
		$hiddenBlocks.removeClass('is-open');
	}
});

$(window).on('resize', () => {
	$('.js-open-menu.is-open').trigger('click');
});



